:root {
  font-size: 10px;
  margin: 0px;
  padding: 0px;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#content,
.container {
  min-width: 100vw;
  min-height: 100vh;
  margin: 0px;
  padding: 0px;
}
.container {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 1rem;
}

.footer {
  align-self: center;
  font-weight: 600;
  flex-grow: 0;
  padding: 1rem;
}
.header {
  font-size: 5rem;
  margin: 0;
  padding: 0;
  height: 6rem;
  flex-grow: 0;
}

.general-info,
.education,
.experience {
  flex-grow: 1;
  border-bottom: solid 3px #f4f4;
}

#form-general,
#form-general-edit,
#form-education,
#form-education-edit,
#form-experience,
#form-experience-edit {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-start;
  gap: 1rem;
  border: solid 1px;
  padding: 1rem;
}
#form-general div,
#form-general-edit div,
#form-education div,
#form-education-edit div,
#form-experience div,
#form-experience-edit div {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-start;
  gap: 1rem;
}
.form-general,
.form-general-edit,
.form-education,
.form-education-edit,
.form-experience,
.form-experience-edit {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) scale(0);
  transition: 200ms ease-in-out;
  background-color: rgb(246, 225, 220);
  z-index: 10;
  padding: 2rem;
  max-width: 60%;
  border-radius: 10px;
}
.form-general.active,
.form-general-edit.active,
.form-education.active,
.form-education-edit.active,
.form-experience.active,
.form-experience-edit.active {
  transform: translate(-50%, -50%) scale(1);
}

button {
  border: none;
  border-radius: 1rem;
  min-width: 8rem;
}

button:hover {
  background-color: white;
}

.section {
  font-size: 1.5rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  gap: 1rem;
  border-bottom: solid 1px #e5ffde;
  padding: 1rem;
}

.section-part {
  display: grid;
  grid-template-columns: 15rem 30rem;
  gap: 10rem;
}

.section-head {
  font-weight: 700;
}
